import axios from 'axios'
import {Message} from 'element-ui'
import store from '@/store'
import {getToken} from '@/request/token'

const service = axios.create({
  baseURL: process.env.BASE_API,
  timeout: 10000
})

//requestÊã¶Êà™Âô®
service.interceptors.request.use(config => {

  if (store.state.token) {
    config.headers['Oauth-Token'] = getToken()
  }
  return config
}, error => {

  Promise.reject(error)
})

// responeÊã¶Êà™Âô®
service.interceptors.response.use(
  response => {

    //ÂÖ®Â±ÄÁªü‰∏ÄÂ§ÑÁêÜ SessionË∂ÖÊó∂
    if (response.headers['session_time_out'] == 'timeout') {
      store.dispatch('fedLogOut')
    }

    const res = response.data;

    //0 ‰∏∫ÊàêÂäüÁä∂ÊÄÅ
    if (res.code !== 0) {

      //90001 SessionË∂ÖÊó∂
      if (res.code === 90001) {
        return Promise.reject('error');
      }

      //20001 Áî®Êà∑Êú™ÁôªÂΩï
      if (res.code === 20001) {
        console.info("Áî®Êà∑Êú™ÁôªÂΩï")

        Message({
          type: 'warning',
          showClose: true,
          message: 'Êú™ÁôªÂΩïÊàñÁôªÂΩïË∂ÖÊó∂ÔºåËØ∑ÈáçÊñ∞ÁôªÂΩïÂì¶'
        })

        return Promise.reject('error');
      }

      //70001 ÊùÉÈôêËÆ§ËØÅÈîôËØØ
      if (res.code === 70001) {
        console.info("ÊùÉÈôêËÆ§ËØÅÈîôËØØ")
        Message({
          type: 'warning',
          showClose: true,
          message: '‰Ω†Ê≤°ÊúâÊùÉÈôêËÆøÈóÆÂì¶'
        })
        return Promise.reject('error');
      }

      return Promise.reject(res.msg);
    } else {
      return response.data;
    }
  },
  error => {
    Message({
      type: 'warning',
      showClose: true,
      message: 'ËøûÊé•Ë∂ÖÊó∂'
    })
    return Promise.reject('error')
  })

export default service



